
#app .tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
}
#app .tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  border-bottom: 0.07em solid rgba(7,17,27,0.1);
}
#app .tab .tab-item > a {
  display: block;
  color: #4d555d;
}
#app .tab .tab-item > a.router-link-active {
  color: #f01414;
}


