
.app {
  width: 80px;
  height: 25px;
  border: 1px solid #00f;
  position: relative;
}
.app .greenSquare {
  display: inline-block;
  width: 25px;
  height: 25px;
  background: #008000;
}
.app .redSquare {
  display: inline-block;
  position: absolute;
  right: 0;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  opacity: 1;
}
.app .redSquare .inner {
  display: inline-block;
  width: 25px;
  height: 25px;
  background: #f00;
  -webkit-transition: all 0.7s;
  transition: all 0.7s;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.app .redSquare.move-enter-active,
.app .redSquare.move-leave-active {
  -webkit-transition: all 0.7s;
  transition: all 0.7s;
}
.app .redSquare.move-enter,
.app .redSquare.move-leave-to {
  -webkit-transform: translate3d(-20px, 0, 0);
          transform: translate3d(-20px, 0, 0);
  opacity: 0;
}
.app .redSquare.move-enter .inner,
.app .redSquare.move-leave-to .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}


