
@font-face {
  font-family: 'sell-icon';
  src: url("../../common/fonts/sell-icon.eot?nbq4u4");
  src: url("../../common/fonts/sell-icon.eot?nbq4u4#iefix") format('embedded-opentype'), url("../../common/fonts/sell-icon.ttf?nbq4u4") format('truetype'), url("../../common/fonts/sell-icon.woff?nbq4u4") format('woff'), url("../../common/fonts/sell-icon.svg?nbq4u4#sell-icon") format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
/* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'sell-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
/* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-add_circle:before {
  content: "\e908";
}
.icon-arrow_lift:before {
  content: "\e900";
}
.icon-check_circle:before {
  content: "\e905";
}
.icon-close:before {
  content: "\e906";
}
.icon-favorite:before {
  content: "\e904";
}
.icon-keyboard_arrow_right:before {
  content: "\e909";
}
.icon-remove_circle_outline:before {
  content: "\e907";
}
.icon-shopping_cart:before {
  content: "\e903";
}
.icon-thumb_down:before {
  content: "\e902";
}
.icon-thumb_up:before {
  content: "\e901";
}
.cartcontrol {
  font-size: 0;
  position: relative;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  position: absolute;
  left: 0;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.cartcontrol .cart-decrease .inner {
  font-size: 24px;
  display: inline-block;
  color: #00a0dc;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cartcontrol .cart-decrease.move-enter-active,
.cartcontrol .cart-decrease.move-leave-active {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease.move-enter,
.cartcontrol .cart-decrease.move-leave-to {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0);
          transform: translate3d(24px, 0, 0);
}
.cartcontrol .cart-decrease.move-enter .inner,
.cartcontrol .cart-decrease.move-leave-to .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartcontrol .cart-add {
  font-size: 24px;
  display: inline-block;
  color: #00a0dc;
  position: absolute;
  right: 0;
}
.cartcontrol .cart-count {
  font-size: 10px;
  display: inline-block;
  color: #93999f;
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  margin-left: 4px;
  position: absolute;
  left: 24px;
}


